<template>
	<div>
		<div class="pay-container">
			<div class="pay_center">
				<img :src="state[res].Img" alt>
			</div>
			<div class="pay_center pay_title">{{state[res].content}}</div>
			<div class="pay_center">
				<button class="pay_go_shop" @click="goShop">继续购物</button>
			</div>
		</div>
		<Footer></Footer>
	</div>
</template>

<script>
	import Footer from "@/components/footer/Footer";

	export default {
		name: "Pay",

		components: {
			Footer
		},

		data() {
			return {
				state: [
					{Img: "static/img/pay/pay_success.png", content: "您已成功下单，感谢您的购买！"},
					{Img: "static/img/pay/pay_error.png", content: "下单失败，请重新下单"}
				]
			};
		},

		created() {
			this.res = this.$route.query.result;
		},

		methods: {
			goShop() {
				this.$router.push('/index');
			}
		},
	};
</script>

<style scoped>
	.pay-container {
		margin: 160px auto;
		width: 80%;
		min-width: 1000px;
	}

	.pay_center {
		margin-top: 15px;
		margin-bottom: 15px;
		text-align: center;
	}

	.pay_title {
		font-size: 20px;
	}

	.pay_cut_off {
		width: 800px;
		height: 2px;
		background: #f7f7f7;
		margin: auto;
	}

	.pay_go_shop {
		color: white;
		padding: 10px 20px;
		background: #e11e46;
		border: none;
	}

	.pay_width {
		width: 800px;
		margin: auto;
	}

	.pay_p {
		font-size: 16px;
		color: #a5a5a5;
		margin-top: 15px;
	}

	.pay_address {
		padding-top: 5px;
		padding-bottom: 15px;
		font-size: 18px;
	}
</style>



